<template id="">
    <div id="wrapper">
      <h1>Log in to enjoy miniTrello!</h1>
      <form @submit.prevent="loginUser">
        <input v-model="user.login" type="text" placeholder="Login">
        <input v-model="user.password" type="password" placeholder="Password">
        <button class="btn btn-success" type="submit" name="button">Log in</button>
      </form>
    </div>
</template>

<script>
  export default {
    name: 'app-login',
    data() {
      return {
        user: {
          login: '',
          password: ''
        }
      }
    },
    methods: {
      loginUser () {
        firebase.auth().signInWithEmailAndPassword(this.user.login, this.user.password)
          .then(response => {
            const settings = {
                login: response.email,
                uid: response.uid,
                logComplete: true
            }
            this.$emit('addUser', settings)
          })
      }
    }
  }
</script>

<style>
#wrapper {
  position: absolute;
  top: 50%;
  right: 0;
  width: 50%;
  margin-top: -150px;
  margin-right: 25%;
  background: #fff;
  text-align: center;
  padding: 30px;
  border-radius: 8px;
}
  h1 {
    height: 100px;
    line-height: 100px;
    margin: 0;
  }
  form {
    padding: 50px 0;
    display: inline-block;
    width: 70%;
  }
  form input {
    width: 33%;
    height: 30px;
  }
  form button {
    height: 30px;
    width: 15%;
  }
</style>



